// lib/api/video-common.ts
import axios from 'axios';

/**
 * Shared utilities, types and helpers for video APIs
 */

export interface VideoSource {
  url: string;
  quality: string;
  type: "iframe";
  servers: string;
  tmdbId: number;
  mediaType: "movie" | "tv";
  season?: number;
  episode?: number;
  title?: string;
  episodeTitle?: string;
  responseTime?: number;
  working?: boolean;
  error?: string;
  provider?: string;
}

export interface VideoProvider {
  name: string;
  value: string;
  priority: number;
  mediaTypes: ("movie" | "tv")[];
  isTemplate: boolean;
  hostname: string;
}

export interface EmbedCheckResponse {
  success: boolean;
  workingUrl?: {
    provider: string;
    url: string;
    originalUrl: string;
    type: string;
  } | null;
  totalTested: number;
  workingCount: number;
  allUrls: Array<{
    provider: string;
    url: string;
    originalUrl: string;
    type: string;
    working: boolean;
    responseTime?: number;
    error?: string;
  }>;
  metadata: {
    mediaType: string;
    tmdbId: number;
    season?: number;
    episode?: number;
    tested: boolean;
    timestamp: string;
  };
}

/** Shared ENV (generic fallback providers) */
export const COMMON_ENV = {
  BASE: (process.env.NEXT_PUBLIC_VIDEO_API_BASE || "").trim(),
  E1: (process.env.NEXT_PUBLIC_VIDEO_EMBED_1 || "").trim(),
  E2: (process.env.NEXT_PUBLIC_VIDEO_EMBED_2 || "").trim(),
  E3: (process.env.NEXT_PUBLIC_VIDEO_EMBED_3 || "").trim(),
  E4: (process.env.NEXT_PUBLIC_VIDEO_EMBED_4 || "").trim(),
  TMDB_API_KEY: (process.env.NEXT_PUBLIC_TMDB_API_KEY || "").trim()
};

export const EXTRA_PARAMS = (process.env.NEXT_PUBLIC_VIDEO_URL_PARAMS || "").trim();

/** Helpers */
function isTemplateLocal(url?: string) {
  if (!url) return false;
  return /\$\{(id|type|season|episode)\}/.test(url);
}

export function getServerLabel(url: string): string {
  if (!url) return 'Unknown Server';
  try {
    const hostname = new URL(url).hostname;
    return hostname.replace(/^www\./, '');
  } catch {
    return 'Unknown Server';
  }
}

export function normalizeBase(url: string) {
  if (!url) return '';
  return url.endsWith('/') ? url.slice(0, -1) : url;
}

export function appendParams(url: string, params: string) {
  if (!params) return url;
  return url + (url.includes('?') ? '&' : '?') + params;
}

export function buildEmbedPath(mediaType: "movie" | "tv", tmdbId: number, season?: number, episode?: number) {
  if (mediaType === 'movie') return `/movie/${tmdbId}`;
  if (mediaType === 'tv') {
    if (season && episode) return `/tv/${tmdbId}/${season}/${episode}`;
    return `/tv/${tmdbId}`;
  }
  throw new Error('invalid mediaType');
}

export function buildRawUrl(providerValue: string, mediaType: "movie" | "tv", tmdbId: number, season?: number, episode?: number) {
  if (!providerValue) throw new Error('providerValue required');
  if (isTemplateLocal(providerValue)) {
    return providerValue
      .replace(/\$\{id\}/g, String(tmdbId))
      .replace(/\$\{type\}/g, mediaType)
      .replace(/\$\{season\}/g, String(season || 1))
      .replace(/\$\{episode\}/g, String(episode || 1));
  }
  const base = normalizeBase(providerValue);
  const path = buildEmbedPath(mediaType, tmdbId, season, episode);
  return `${base}${path}`;
}

export function buildEmbedUrl(providerValue: string, mediaType: "movie" | "tv", tmdbId: number, season?: number, episode?: number) {
  const raw = buildRawUrl(providerValue, mediaType, tmdbId, season, episode);
  return appendParams(raw, EXTRA_PARAMS);
}

export function getEmbedUrlCandidatesFromProviders(providers: VideoProvider[], mediaType: "movie" | "tv", tmdbId: number, season?: number, episode?: number) {
  const urls = providers.map(p => buildEmbedUrl(p.value, mediaType, tmdbId, season, episode));
  return [...new Set(urls)];
}

/** Fallback provider list built from COMMON_ENV (used if module-specific providers are missing) */
export const COMMON_PROVIDERS: VideoProvider[] = [
  { name: 'Primary', value: COMMON_ENV.E1, priority: 1, mediaTypes: ['movie','tv'], isTemplate: isTemplateLocal(COMMON_ENV.E1), hostname: getServerLabel(COMMON_ENV.E1) },
  { name: 'Secondary', value: COMMON_ENV.E2, priority: 2, mediaTypes: ['movie','tv'], isTemplate: isTemplateLocal(COMMON_ENV.E2), hostname: getServerLabel(COMMON_ENV.E2) },
  { name: 'Tertiary', value: COMMON_ENV.E3, priority: 3, mediaTypes: ['movie','tv'], isTemplate: isTemplateLocal(COMMON_ENV.E3), hostname: getServerLabel(COMMON_ENV.E3) },
  { name: 'Backup', value: COMMON_ENV.E4, priority: 4, mediaTypes: ['movie','tv'], isTemplate: isTemplateLocal(COMMON_ENV.E4), hostname: getServerLabel(COMMON_ENV.E4) },
  { name: 'Base', value: COMMON_ENV.BASE, priority: 5, mediaTypes: ['movie','tv'], isTemplate: isTemplateLocal(COMMON_ENV.BASE), hostname: getServerLabel(COMMON_ENV.BASE) }
].filter(p => !!p.value);

/** checkEmbedUrls helper (client convenience: calls your /api/check-embed route) */
export async function checkEmbedUrls(mediaType: "movie" | "tv", tmdbId: number, season?: number, episode?: number, testUrls: boolean = true): Promise<EmbedCheckResponse | null> {
  try {
    const res = await fetch('/api/check-embed', {
      method: 'POST',
      headers: {'content-type':'application/json'},
      body: JSON.stringify({ mediaType, tmdbId, season, episode, testUrls })
    });
    if (!res.ok) return null;
    const payload = await res.json();
    return payload as EmbedCheckResponse;
  } catch (err) {
    console.error('checkEmbedUrls error', err);
    return null;
  }
}

/** resolveTmdbId helper (numeric or imdb -> tmdb) */
export async function resolveTmdbId(input: string | number, type: "movie" | "tv"): Promise<number | null> {
  if (typeof input === 'number' && Number.isFinite(input)) return input;
  const s = String(input).trim();
  if (!s) return null;
  const asNum = Number(s);
  if (Number.isFinite(asNum)) return asNum;
  if (/^tt\d+$/i.test(s)) {
    if (!COMMON_ENV.TMDB_API_KEY) return null;
    try {
      const { data } = await axios.get(`https://api.themoviedb.org/3/find/${s}`, {
        params: { api_key: COMMON_ENV.TMDB_API_KEY, external_source: 'imdb_id' },
        timeout: 5000
      });
      const results = type === 'movie' ? data.movie_results : data.tv_results;
      return results?.[0]?.id ?? null;
    } catch (err) {
      console.warn('resolveTmdbId failed', err);
      return null;
    }
  }
  return null;
}

/** create VideoSource object */
export function createVideoSourceFromProvider(provider: VideoProvider, mediaType: "movie" | "tv", tmdbId: number, season?: number, episode?: number, additional: Partial<VideoSource> = {}): VideoSource {
  const url = buildEmbedUrl(provider.value, mediaType, tmdbId, season, episode);
  return {
    url,
    quality: 'HD',
    type: 'iframe',
    servers: `${provider.name} (${provider.hostname})`,
    tmdbId,
    mediaType,
    season,
    episode,
    provider: provider.name,
    ...additional
  };
}

/** config validator */
export function validateConfiguration(providers: VideoProvider[] = COMMON_PROVIDERS) {
  const errors: string[] = [];
  const warnings: string[] = [];
  if (!providers || providers.length === 0) errors.push('No providers configured');
  if (!COMMON_ENV.TMDB_API_KEY) warnings.push('TMDB API key not set (imdb resolution disabled)');
  return { isValid: errors.length === 0, errors, warnings };
}

export { COMMON_ENV as ENV, COMMON_PROVIDERS };
