// lib/api/video-movie.ts
import axios from 'axios';
import {
  VideoSource,
  VideoProvider,
  EmbedCheckResponse,
  createVideoSourceFromProvider,
  buildEmbedUrl,
  getEmbedUrlCandidatesFromProviders,
  resolveTmdbId,
  checkEmbedUrls,
  validateConfiguration,
  getServerLabel,
  COMMON_PROVIDERS
} from './video-common';

/**
 * Movie-specific provider/env processing.
 * Module looks for NEXT_PUBLIC_MOVIE_EMBED_1..4 and NEXT_PUBLIC_MOVIE_API_BASE
 * If not present, falls back to COMMON_PROVIDERS.
 */

/** Build providers from env if present */
function buildMovieProviders(): VideoProvider[] {
  const E1 = (process.env.NEXT_PUBLIC_MOVIE_EMBED_1 || "https://cinemaos.tech/player/${id}").trim();
  const E2 = (process.env.NEXT_PUBLIC_MOVIE_EMBED_2 || "").trim();
  const E3 = (process.env.NEXT_PUBLIC_MOVIE_EMBED_3 || "").trim();
  const E4 = (process.env.NEXT_PUBLIC_MOVIE_EMBED_4 || "").trim();
  const BASE = (process.env.NEXT_PUBLIC_MOVIE_API_BASE || "").trim();

  const items: VideoProvider[] = [
    { name: 'Primary', value: E1, priority: 1, mediaTypes: ['movie'], isTemplate: /\$\{(id|type|season|episode)\}/.test(E1), hostname: getServerLabel(E1) },
    { name: 'Secondary', value: E2, priority: 2, mediaTypes: ['movie'], isTemplate: /\$\{(id|type|season|episode)\}/.test(E2), hostname: getServerLabel(E2) },
    { name: 'Tertiary', value: E3, priority: 3, mediaTypes: ['movie'], isTemplate: /\$\{(id|type|season|episode)\}/.test(E3), hostname: getServerLabel(E3) },
    { name: 'Backup', value: E4, priority: 4, mediaTypes: ['movie'], isTemplate: /\$\{(id|type|season|episode)\}/.test(E4), hostname: getServerLabel(E4) },
    { name: 'Base', value: BASE, priority: 5, mediaTypes: ['movie'], isTemplate: /\$\{(id|type|season|episode)\}/.test(BASE), hostname: getServerLabel(BASE) }
  ].filter(p => !!p.value);

  // if none specified, fall back to common providers that support movie
  if (items.length === 0) {
    return COMMON_PROVIDERS.filter(p => p.mediaTypes.includes('movie'));
  }
  return items;
}

/** Get movie providers (computed) */
const MOVIE_PROVIDERS = buildMovieProviders();

/** Build VideoSource list for a movie */
export async function getMovieSources(tmdbId: number): Promise<VideoSource[]> {
  if (!tmdbId || tmdbId <= 0) throw new Error(`Invalid movie TMDB ID: ${tmdbId}`);
  const cfg = validateConfiguration(MOVIE_PROVIDERS);
  if (!cfg.isValid) throw new Error('video configuration invalid: ' + cfg.errors.join('; '));

  const sources = MOVIE_PROVIDERS.map(provider => {
    const s = createVideoSourceFromProvider(provider, 'movie', tmdbId, undefined, undefined, { title: `Movie ${tmdbId}` });
    s.servers = `${provider.name} (${provider.hostname || getServerLabel(provider.value)})`;
    s.provider = provider.name;
    return s;
  });

  return sources;
}

/** Test via server-side /api/check-embed (optional) and map results */
export async function getMovieSourcesWithTesting(tmdbId: number, testUrls: boolean = true): Promise<{
  sources: VideoSource[];
  workingSources: VideoSource[];
  embedCheckData: EmbedCheckResponse | null;
}> {
  const sources = await getMovieSources(tmdbId);
  if (!testUrls) return { sources, workingSources: sources, embedCheckData: null };

  const check = await checkEmbedUrls('movie', tmdbId, undefined, undefined, true);
  if (!check) return { sources, workingSources: sources, embedCheckData: null };

  const workingSources = check.allUrls.filter(u => u.working).map(u => {
    const original = sources.find(s => s.url === u.originalUrl) || sources[0];
    return { ...original, url: u.url, working: u.working, responseTime: u.responseTime, error: u.error, provider: u.provider, servers: `${u.provider} (${u.working ? 'Working' : 'Failed'})` };
  });

  return { sources, workingSources, embedCheckData: check };
}

/** Get movie metadata + best embed (first working or first) */
export async function getMovieWithEmbed(tmdbIdInput: number | string, testUrls: boolean = true) {
  const tmdbId = await resolveTmdbId(tmdbIdInput, 'movie');
  if (!tmdbId) throw new Error('unresolvable tmdb id');

  // fetch TMDB metadata (optional)
  const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY?.trim();
  const movieResp = API_KEY ? await axios.get(`https://api.themoviedb.org/3/movie/${tmdbId}`, { params: { api_key: API_KEY }, timeout: 10000 }).catch(()=>({data:null})) : { data: null };

  const { sources, workingSources, embedCheckData } = await getMovieSourcesWithTesting(tmdbId, testUrls);

  if (movieResp?.data?.title) {
    sources.forEach(s => s.title = movieResp.data.title);
    workingSources.forEach(s => s.title = movieResp.data.title);
  }

  const embedUrl = workingSources[0]?.url || sources[0]?.url || '';

  return {
    movie: movieResp?.data,
    embedUrl,
    sources,
    workingSources,
    embedCheckData,
    metadata: {
      tmdbId,
      mediaType: 'movie' as const,
      title: movieResp?.data?.title,
      sourcesAvailable: sources.length,
      workingSourcesCount: workingSources.length,
      tested: testUrls,
      timestamp: new Date().toISOString()
    }
  };
}

/** Helpers */
export function getMovieEmbedCandidates(tmdbId: number) {
  return getEmbedUrlCandidatesFromProviders(MOVIE_PROVIDERS, 'movie', tmdbId);
}

export const movieVideoApi = {
  getMovieSources,
  getMovieSourcesWithTesting,
  getMovieWithEmbed,
  getMovieEmbedCandidates
};

export default movieVideoApi;
