// lib/api/video-tv.ts
import axios from 'axios';
import {
  VideoSource,
  VideoProvider,
  EmbedCheckResponse,
  createVideoSourceFromProvider,
  getEmbedUrlCandidatesFromProviders,
  resolveTmdbId,
  checkEmbedUrls,
  validateConfiguration,
  getServerLabel,
  COMMON_PROVIDERS
} from './video-common';

/** Build TV providers from env (module-level) */
function buildTVProviders(): VideoProvider[] {
  const E1 = (process.env.NEXT_PUBLIC_TV_EMBED_1 || "https://cinemaos.tech/player/${id}/${season}/${episode}").trim();
  const E2 = (process.env.NEXT_PUBLIC_TV_EMBED_2 || "").trim();
  const E3 = (process.env.NEXT_PUBLIC_TV_EMBED_3 || "").trim();
  const E4 = (process.env.NEXT_PUBLIC_TV_EMBED_4 || "").trim();
  const BASE = (process.env.NEXT_PUBLIC_TV_API_BASE || "").trim();

  const items: VideoProvider[] = [
    { name: 'Primary', value: E1, priority: 1, mediaTypes: ['tv'], isTemplate: /\$\{(id|type|season|episode)\}/.test(E1), hostname: getServerLabel(E1) },
    { name: 'Secondary', value: E2, priority: 2, mediaTypes: ['tv'], isTemplate: /\$\{(id|type|season|episode)\}/.test(E2), hostname: getServerLabel(E2) },
    { name: 'Tertiary', value: E3, priority: 3, mediaTypes: ['tv'], isTemplate: /\$\{(id|type|season|episode)\}/.test(E3), hostname: getServerLabel(E3) },
    { name: 'Backup', value: E4, priority: 4, mediaTypes: ['tv'], isTemplate: /\$\{(id|type|season|episode)\}/.test(E4), hostname: getServerLabel(E4) },
    { name: 'Base', value: BASE, priority: 5, mediaTypes: ['tv'], isTemplate: /\$\{(id|type|season|episode)\}/.test(BASE), hostname: getServerLabel(BASE) }
  ].filter(p => !!p.value);

  if (items.length === 0) {
    return COMMON_PROVIDERS.filter(p => p.mediaTypes.includes('tv'));
  }
  return items;
}

const TV_PROVIDERS = buildTVProviders();

/** Get TV sources for a given S/E */
export async function getTVSources(tmdbId: number, season?: number, episode?: number): Promise<VideoSource[]> {
  if (!tmdbId || tmdbId <= 0) throw new Error(`Invalid TV TMDB ID: ${tmdbId}`);
  const sNum = season || 1;
  const eNum = episode || 1;

  const cfg = validateConfiguration(TV_PROVIDERS);
  if (!cfg.isValid) throw new Error('video configuration invalid: ' + cfg.errors.join('; '));

  const sources = TV_PROVIDERS.map(provider => {
    const src = createVideoSourceFromProvider(provider, 'tv', tmdbId, sNum, eNum, { title: `TV ${tmdbId}`, episodeTitle: `S${sNum}E${eNum}` });
    src.servers = `${provider.name} (${provider.hostname || getServerLabel(provider.value)})`;
    src.provider = provider.name;
    return src;
  });

  return sources;
}

/** Test via /api/check-embed and map results */
export async function getTVSourcesWithTesting(tmdbId: number, season?: number, episode?: number, testUrls: boolean = true) {
  const sources = await getTVSources(tmdbId, season, episode);
  if (!testUrls) return { sources, workingSources: sources, embedCheckData: null as EmbedCheckResponse | null };

  const check = await checkEmbedUrls('tv', tmdbId, season, episode, true);
  if (!check) return { sources, workingSources: sources, embedCheckData: null };

  const workingSources = check.allUrls.filter(u => u.working).map(u => {
    const original = sources.find(s => s.url === u.originalUrl) || sources[0];
    return { ...original, url: u.url, working: u.working, responseTime: u.responseTime, error: u.error, provider: u.provider, servers: `${u.provider} (${u.working ? 'Working' : 'Failed'})` };
  });

  return { sources, workingSources, embedCheckData: check };
}

/** Get episode / tv metadata + embed selection */
export async function getTVShowWithEmbed(tmdbIdInput: number | string, season?: number, episode?: number, testUrls: boolean = true) {
  const tmdbId = await resolveTmdbId(tmdbIdInput, 'tv');
  if (!tmdbId) throw new Error('unresolvable tmdb id');

  const sNum = season || 1;
  const eNum = episode || 1;

  // optionally fetch tv/episode metadata (if api key present)
  const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY?.trim();
  let tvInfo: any = null;
  if (API_KEY) {
    try {
      const [ep, seas, show] = await Promise.all([
        axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}/season/${sNum}/episode/${eNum}`, { params: { api_key: API_KEY }, timeout: 8000 }).catch(()=>({data:null})),
        axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}/season/${sNum}`, { params: { api_key: API_KEY }, timeout: 8000 }).catch(()=>({data:null})),
        axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}`, { params: { api_key: API_KEY }, timeout: 8000 }).catch(()=>({data:null}))
      ]);
      tvInfo = { episode: ep.data, season: seas.data, show: show.data };
    } catch (err) {
      tvInfo = null;
    }
  }

  const { sources, workingSources, embedCheckData } = await getTVSourcesWithTesting(tmdbId, sNum, eNum, testUrls);

  if (tvInfo?.show?.name || tvInfo?.episode?.name) {
    const showTitle = tvInfo.show?.name;
    const episodeTitle = tvInfo.episode?.name;
    sources.forEach(s => { s.title = showTitle; s.episodeTitle = episodeTitle || `S${sNum}E${eNum}`; });
    workingSources.forEach(s => { s.title = showTitle; s.episodeTitle = episodeTitle || `S${sNum}E${eNum}`; });
  }

  const embedUrl = workingSources[0]?.url || sources[0]?.url || '';

  return {
    tvShow: tvInfo?.show,
    episode: tvInfo?.episode,
    season: tvInfo?.season,
    embedUrl,
    sources,
    workingSources,
    embedCheckData,
    metadata: {
      tmdbId,
      mediaType: 'tv' as const,
      season: sNum,
      episode: eNum,
      title: tvInfo?.show?.name,
      episodeTitle: tvInfo?.episode?.name,
      tested: testUrls,
      timestamp: new Date().toISOString(),
      sourcesAvailable: sources.length,
      workingSourcesCount: workingSources.length
    }
  };
}

/** Helpers */
export function getTVEmbedCandidates(tmdbId: number, season?: number, episode?: number) {
  return getEmbedUrlCandidatesFromProviders(TV_PROVIDERS, 'tv', tmdbId, season, episode);
}

export const tvVideoApi = {
  getTVSources,
  getTVSourcesWithTesting,
  getTVShowWithEmbed,
  getTVEmbedCandidates
};

export default tvVideoApi;
