// lib/api/video-api.ts
/**
 * Backwards-compatible shim that delegates to movie/tv APIs.
 * Prefer importing movieVideoApi or tvVideoApi directly from video-movie / video-tv.
 */

export * from './video-common';
export * from './video-movie';
export * from './video-tv';

import * as movieModule from './video-movie';
import * as tvModule from './video-tv';
import * as commonModule from './video-common';

function fnOrFallback(maybeFn: any, fallback: any) {
  return typeof maybeFn === 'function' ? maybeFn : fallback;
}

const noopAsync = async (..._args: any[]) => null;
const returnEmptyArray = async (..._args: any[]) => [];
const returnFalse = () => false;
const returnEmptyObject = () => ({});
const returnEmptyString = () => "";

const movie = {
  getMovieSources: fnOrFallback(movieModule.getMovieSources, returnEmptyArray),
  getMovieSourcesWithTesting: fnOrFallback(movieModule.getMovieSourcesWithTesting, movieModule.getMovieSources ?? returnEmptyArray),
  getMovieWithEmbed: fnOrFallback(movieModule.getMovieWithEmbed, noopAsync),
  getMovieEmbedCandidates: fnOrFallback(movieModule.getMovieEmbedCandidates, (id:number) => (commonModule.getEmbedUrlCandidatesFromProviders ? commonModule.getEmbedUrlCandidatesFromProviders((commonModule as any).COMMON_PROVIDERS || [], 'movie', id) : [])),
  hasMovieSourcesConfigured: fnOrFallback((movieModule as any).hasMovieProviders ?? (() => false), () => false),
};

const tv = {
  getTVSources: fnOrFallback(tvModule.getTVSources, returnEmptyArray),
  getTVSourcesWithTesting: fnOrFallback(tvModule.getTVSourcesWithTesting, tvModule.getTVSources ?? returnEmptyArray),
  getTVShowWithEmbed: fnOrFallback(tvModule.getTVShowWithEmbed, noopAsync),
  getTVEmbedCandidates: fnOrFallback(tvModule.getTVEmbedCandidates, (id:number,s?:number,e?:number) => (commonModule.getEmbedUrlCandidatesFromProviders ? commonModule.getEmbedUrlCandidatesFromProviders((commonModule as any).COMMON_PROVIDERS || [], 'tv', id, s, e) : [])),
  hasTVSourcesConfigured: fnOrFallback((tvModule as any).hasTVProviders ?? (() => false), () => false),
};

const common = {
  getEmbedUrlCandidatesFromProviders: fnOrFallback((commonModule as any).getEmbedUrlCandidatesFromProviders, () => []),
  checkEmbedUrls: fnOrFallback((commonModule as any).checkEmbedUrls, async () => null),
  getConfigurationStatus: fnOrFallback((commonModule as any).getConfigurationStatus ?? (() => ({})), () => ({})),
  validateConfiguration: fnOrFallback((commonModule as any).validateConfiguration ?? (() => ({ isValid:false, errors:[], warnings:[] })), () => ({ isValid:false, errors:[], warnings:[] })),
  VIDEO_PROVIDERS: (commonModule as any).COMMON_PROVIDERS || []
};

export const videoApi = {
  async getMovieSources(tmdbId: number) { return movie.getMovieSources(tmdbId); },
  async getMovieSourcesWithTesting(tmdbId: number) { return movie.getMovieSourcesWithTesting(tmdbId); },
  async getMovieWithEmbed(tmdbId: number) { return movie.getMovieWithEmbed(tmdbId); },

  async getTVSources(tmdbId: number, season?: number, episode?: number) { return tv.getTVSources(tmdbId, season, episode); },
  async getTVSourcesWithTesting(tmdbId: number, season?: number, episode?: number) { return tv.getTVSourcesWithTesting(tmdbId, season, episode); },
  async getTVShowWithEmbed(tmdbId: number, season?: number, episode?: number) { return tv.getTVShowWithEmbed(tmdbId, season, episode); },

  hasConfiguredApis() { return Array.isArray(common.VIDEO_PROVIDERS) && common.VIDEO_PROVIDERS.length > 0; },
  getConfigStatus() { return common.getConfigurationStatus(); },
  getEmbedUrlCandidates(mediaType: 'movie'|'tv', tmdbId: number, season?: number, episode?: number) { return common.getEmbedUrlCandidatesFromProviders(common.VIDEO_PROVIDERS || [], mediaType, tmdbId, season, episode); },
  logConfiguration() { if (typeof (commonModule as any).logConfiguration === 'function') (commonModule as any).logConfiguration(); },

  movie,
  tv,
  common
};

export default videoApi;

/** Named legacy exports */
export const getMovieSources = movie.getMovieSources;
export const getMovieSourcesWithTesting = movie.getMovieSourcesWithTesting;
export const getMovieWithEmbed = movie.getMovieWithEmbed;
export const getTVSources = tv.getTVSources;
export const getTVSourcesWithTesting = tv.getTVSourcesWithTesting;
export const getTVShowWithEmbed = tv.getTVShowWithEmbed;
export const checkEmbedUrls = common.checkEmbedUrls;
export const VIDEO_PROVIDERS = common.VIDEO_PROVIDERS;
