'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { ArrowLeft } from 'lucide-react';
import { useEmbedAutoSwitch } from '@/lib/hooks/useEmbedAutoSwitch';

interface TvPlayerProps {
  mediaId: number;
  season: number;
  episode: number;
  title?: string;
  onClose?: () => void;
  guardMs?: number;
  showControls?: boolean;
  autoNextMs?: number; // crude auto-next timer (optional)
}

export default function TvPlayer({
  mediaId,
  season,
  episode,
  title,
  onClose,
  guardMs = 5000,
  showControls = true,
  autoNextMs,
}: TvPlayerProps) {
  const router = useRouter();
  const { src, index, total, onLoad, onError, next, prev } = useEmbedAutoSwitch({
    mediaType: 'tv',
    mediaId,
    season,
    episode,
    guardMs,
    preferServer: true,
  });

  const handleClose = () => (onClose ? onClose() : router.back());

  useEffect(() => {
    if (!autoNextMs) return;
    const id = window.setTimeout(() => {
      const nextEp = (episode || 1) + 1;
      router.replace(`/watch/tv/${mediaId}?season=${season}&episode=${nextEp}`, { scroll: false });
    }, autoNextMs);
    return () => clearTimeout(id);
  }, [autoNextMs, episode, season, mediaId, router]);

  return (
    <div className="fixed inset-0 z-50 bg-black">
      {src ? (
        <>
          <iframe
            key={src}
            src={src}
            className="w-full h-full"
            allowFullScreen
            allow="autoplay; encrypted-media; picture-in-picture"
            onLoad={onLoad}
            onError={onError}
            style={{ border: 'none' }}
            title={title || `tv-${mediaId}-s${season}e${episode}`}
          />

          {/* Back + Title + S/E */}
          <div className="absolute top-4 left-4 z-30 flex items-center gap-3">
            <button
              onClick={handleClose}
              className="p-2 rounded-full bg-black/60 text-white"
              aria-label="Back"
            >
              <ArrowLeft className="w-6 h-6" />
            </button>
            {title && (
              <h1 className="text-white font-extrabold text-lg truncate max-w-xs">{title}</h1>
            )}
            <div className="ml-2 text-white/80 text-sm px-2 py-0.5 rounded bg-black/50">
              S{season}E{episode}
            </div>
          </div>

          <div className="absolute top-4 right-4 z-30 text-white/80 text-sm flex items-center gap-3">
            <div>Auto-switch enabled — Press ESC to exit</div>
            <div className="text-xs ml-2">Source {index + 1}/{total || 0}</div>
            {showControls && total > 1 && (
              <div className="ml-3 flex items-center gap-2">
                <button onClick={prev} className="px-2 py-1 bg-black/60 rounded hover:bg-black/80">Prev</button>
                <button onClick={next} className="px-2 py-1 bg-black/60 rounded hover:bg-black/80">Next</button>
              </div>
            )}
          </div>
        </>
      ) : (
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-white/70 text-sm">Preparing player…</div>
        </div>
      )}
    </div>
  );
}
